* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  display: block;
}

.icon {
  width: 1.875rem;
  object-fit: contain;
}

@media (min-width: 768px) {
  .icon {
    width: 18px;
  }
}
.main {
  width: 100%;
  height: 100vh;
  background-color: hsl(210deg, 46%, 95%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Manrope", sans-serif;
  color: hsl(214deg, 17%, 51%);
}

.card {
  width: 90%;
  max-width: 800px;
  background-color: white;
  border-radius: 0.625rem;
  position: relative;
  overflow: hidden;
}
.card__content {
  padding-block: 30px 10px;
  padding-inline: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.card__heading {
  font-weight: 700;
  color: hsl(217deg, 19%, 35%);
  font-size: 17px;
}
.card__text {
  margin-top: 0.9375rem;
  font-size: 0.875rem;
  line-height: 1.5;
  font-weight: 500;
}

.share {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  margin-bottom: 15px;
}
.share__avatar {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50px;
  margin-right: 20px;
}
.share__icon {
  width: 20px;
  background-image: url(../img/icon-share.svg), linear-gradient(hsl(210deg, 46%, 95%) 0%, hsl(210deg, 46%, 95%) 100%);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 3.125rem;
  padding: 18px;
  cursor: pointer;
  transition: background-image 2s ease;
}
.share__name {
  color: hsl(217deg, 19%, 35%);
  font-size: 1rem;
  font-weight: 700;
}
.share__date {
  font-size: 14px;
  color: hsl(214deg, 17%, 51%);
  font-weight: 500;
}

.footer__nav {
  align-items: center;
  background-color: hsl(217deg, 19%, 35%);
  bottom: 0;
  display: flex;
  justify-content: space-around;
  padding: 15px;
  position: absolute;
  width: 104%;
  transform: translateX(93%) scale(0);
  transition: transform 2s ease;
}
.footer__action {
  color: hsl(210deg, 46%, 95%);
  letter-spacing: 5px;
  text-transform: uppercase;
}
.footer__icons {
  display: grid;
  grid-auto-flow: column;
  gap: 30px;
}

.footer__radio {
  display: none;
}
.footer__radio__radio:checked ~ .footer {
  display: block;
}

.footer__radio:checked ~ .footer > .footer__nav {
  transform: translateX(-90%) scale(100%);
}

@media (min-width: 768px) {
  .card {
    width: 790px;
    display: grid;
    grid-template-columns: 1.5fr 2fr;
    overflow: visible;
  }
  .card__image--img {
    border-top-left-radius: 0.625rem;
    border-bottom-left-radius: 0.625rem;
  }
  .card__text {
    font-size: 12px;
  }
  .card__content {
    padding-block: 40px 10px;
    padding-inline: 40px;
  }
  .card__heading {
    font-size: 20px;
  }
  .share {
    justify-content: unset;
  }
  .share__avatar {
    margin-right: 26px;
  }
  .share__icon--none {
    display: none;
  }
  .share__icon {
    margin-left: 380%;
  }
  .footer__nav {
    bottom: 95px;
    width: 30%;
    border-radius: 8px;
    padding-block: 15px;
    transform: scale(0);
    box-shadow: 1px 5px 14px #94a1b7;
  }
  .footer__nav::after {
    content: "";
    background-color: hsl(217deg, 19%, 35%);
    bottom: -12%;
    height: 13px;
    position: absolute;
    transform: rotate(44deg);
    width: 16px;
  }
  .footer__action {
    font-size: 12px;
    letter-spacing: 5px;
    text-transform: uppercase;
    margin-left: 10px;
  }
  .footer__icons {
    gap: 15px;
  }
  .footer__radio:checked ~ .footer > .footer__nav {
    transform: scale(100%);
  }
  .footer__radio:checked + .share__label > .share__icon {
    background-image: url(../img/icon-share2.svg), linear-gradient(hsl(217deg, 19%, 35%) 0%, hsl(217deg, 19%, 35%) 100%);
    background-size: 18px, 100%;
  }
}